@import '~scss/variables';

[dnd-draggable] {
  cursor: move;
}

[dnd-nodrag] {
  cursor: auto;
}

[dnd-list] {
  min-height: $ff-control-min-height;
}

.dndDragging {
  transform: translateZ(0);
}

.dndDraggingSource {
  display: none !important;
}

.dndPlaceholder {
  height: $ff-control-min-height;
  visibility: hidden;
}
