.analytics-dashboard-trash {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 4rem;
  background-color: #fff;
  border-radius: 50%;
  width: 8rem;
  height: 8rem;
  display: none;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 10px rgba(0,0,0,.4);

  .analytics-dragging > & {
    display: flex;
  }

  &.dndDragover {
    color: var(--danger-color);
  }

  .dndPlaceholder {
    display: none !important;
  }
}
