@import '~scss/variables';

.analytics-widget {
  @media (min-width: $screen-lg) {
    flex: 0 0 50%;
  }

  &.analytics-widget-wide {
    flex: 0 0 100%;
  }

  .empty-chart chart {
    &::after {
      content: 'No Data';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    canvas {
      visibility: hidden;
    }
  }

  canvas {
    width: 100% !important;
  }

  .analytics-widget-toolbar {
    flex: none;
    margin: -7px 0;
  }

  ff-btn[sense=drag] button {
    cursor: move;
  }
}

.analytics-dashboard {
  .dndPlaceholder {
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: stretch;
    height: auto;
    min-height: 60px;
    background-color: var(--gray-bg-color);
    color: #fff;
    visibility: visible !important;

    @media (min-width: $screen-lg) {
      flex: 0 0 50%;
    }
  }

  .dndDragover.analytics-wide-placeholder .dndPlaceholder {
    flex: 0 0 100%;
  }
}
